@keyframes ship-animation {
  0% {
    transform: translate(0px, 0px);
  }
  10% {
    transform: translate(20px, -20px);
  }
  40% {
    transform: translate(0, 0px);
  }
  80% {
    transform: translate(-30px, 30px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}
@keyframes car-animation {
  0% {
    transform: translate(0px, 0px);
  }
  10% {
    transform: translate(0, 0);
  }
  40% {
    transform: translate(20px, -20px);
  }
  60% {
    transform: translate(0, 0px);
  }
  80% {
    transform: translate(-30px, 30px);
