@media (max-width: 1440px) {
  .cta__body {
    padding-left: var(--container-offset);
  }
}
@media (max-width: 850px) {
  .cta__body {
    padding: 20px 30px;
    padding-left: 7%;
  }
}
@media (max-width: 600px) {
  .cta__body {
    padding-left: 50px;
  }
}
.cta__body::before {
  content: "";
  position: absolute;
  z-index: -2;
  top: 0;
  right: 0;
  height: 100%;
  width: 100%;
  border-radius: 0 20px 20px 0;
  background-color: var(--dark-blue);
}
@media (max-width: 850px) {
  .cta__body::before {
    right: unset;
    left: var(--container-offset);
    border-radius: 20px 0 0 20px;
  }
}
.cta__body::after {
  content: "";
  position: absolute;
  z-index: -2;
  top: 0;
  right: 0;
  height: 100%;
  width: 100%;
  border-radius: 0 20px 20px 0;
  transform: scale(1.02, 1.05);
  border: 1px solid rgba(15, 0, 161, 0.2);
}
@media (max-width: 850px) {
  .cta__body::after {
    right: unset;
    left: var(--container-offset);
    border-radius: 20px 0 0 20px;
    transform: scale(1.04, 1.05);
  }
}
.cta__title {
  display: block;
  margin-bottom: 30px;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 30px;
  line-height: 140%;
  color: #fff;
}

.cta-form__label span {
  display: block;
  margin-bottom: 10px;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #fff;
}
.cta-form__input {
  display: block;
  width: 100%;
  height: 50px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.2);
  padding: 0 23px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #fff;
}
.cta-form__row {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin-bottom: 20px;
}
@media (max-width: 600px) {
  .cta-form__row {
    grid-template-columns: 1fr;
  }
}
.cta-form__area {
  display: block;
  width: 100%;
  height: 110px;
  overflow-y: auto;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.2);
  scrollbar-width: thin;
  padding: 10px 23px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #fff;
}

.footer {
  background-color: var(--dark-blue);
  padding: 20px 0;
}
@media (any-hover: hover) {
  .footer a:hover {
    text-decoration: underline;
  }
}
.footer__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer__left {
  display: flex;
  align-items: center;
  gap: 100px;
}
@media (max-width: 1440px) {
  .footer__left {
    gap: 50px;
  }
}
.footer__links {
  display: flex;
  align-items: center;
  gap: 24px;
}
.footer__links a {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 16px;
  color: #fff;
}
@media (max-width: 1024px) {
  .footer__links {
    display: none;
  }
}
.footer__links--mob {
  display: none;
}
@media (max-width: 1024px) {
  .footer__links--mob {
    display: flex;
    margin-top: 15px;
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
}
.footer__right {
  display: flex;
  align-items: center;
  gap: 55px;
}
@media (max-width: 850px) {
  .footer__right {
    flex-direction: column;
    align-items: flex-end;
    gap: 10px;
  }
}
.footer__tel {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 24px;
  line-height: 140%;
  color: #fff;
}
@media (max-width: 1440px) {
  .footer__tel {
    font-size: 20px;
  }
}
.footer__mail {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  color: #fff;
}
@media (max-width: 1440px) {
  .footer__mail {
    font-size: 18px;
  }
}
.footer__bottom {
  margin-top: -24px;
  display: flex;
  justify-content: flex-end;
}
@media (max-width: 850px) {
  .footer__bottom {
    margin-top: 0px;
  }
}
.footer__copy {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #fff;
}

.menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background-color: var(--dark-blue);
  transform: translateY(-110%);
  transition: transform 0.3s ease-out;
}
.menu.active {
  transform: translateY(0);
}
.menu__tel {
  display: inline-block !important;
  color: #fff;
}
.menu__body {
  padding: 110px var(--container-offset) 50px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
}